import React, { Fragment, useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import moment from 'moment';
import { makeStyles } from '@material-ui/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import {
  Avatar,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText
} from '@material-ui/core';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import PaymentIcon from '@material-ui/icons/Payment';
import PeopleIcon from '@material-ui/icons/PeopleOutlined';
import CodeIcon from '@material-ui/icons/Code';
import StoreIcon from '@material-ui/icons/Store';

import gradients from 'utils/gradients';
import AttentionDetail from '../../../../views/Attentions/AttentionDetail'
import Slide from '@material-ui/core/Slide';
import { Button } from '@material-ui/core';
import { getAttention } from '../../../../api/api';


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
})

const useStyles = makeStyles(theme => ({
  root: {},
  listItem: {
    '&:hover': {
      backgroundColor: theme.palette.background.default
    }
  },
  avatarBlue: {
    backgroundImage: gradients.blue
  },
  avatarGreen: {
    backgroundImage: gradients.green
  },
  avatarOrange: {
    backgroundImage: gradients.orange
  },
  avatarIndigo: {
    backgroundImage: gradients.indigo
  },
  arrowForwardIcon: {
    color: theme.palette.icon
  }
}));

const NotificationList = props => {
  const { notifications, className, ...rest } = props;

  const classes = useStyles();
  const [openDetailAttention, setOpenDetailAttention] = useState(false);
  const [attention, setAttention] = useState(null)


  const avatars = {
    order: (
      <Avatar className={classes.avatarBlue}>
        <PaymentIcon />
      </Avatar>
    ),
    user: (
      <Avatar className={classes.avatarOrange}>
        <PeopleIcon />
      </Avatar>
    ),
    project: (
      <Avatar className={classes.avatarGreen}>
        <StoreIcon />
      </Avatar>
    ),
    feature: (
      <Avatar className={classes.avatarIndigo}>
        <CodeIcon />
      </Avatar>
    )
  };

  const onShowDetailAttention = id => () => {
    getAttention({ id }).then(response => {
      console.log(response);
      setAttention(response.attention);
      setOpenDetailAttention(true);

    });
  }
  const closeDetailAttention = () => {
    setOpenDetailAttention(false)
  }
  const onRemoveItem = attention => {
    console.log(attention)
    const newNotifications = notifications.filter(notification => notification._id != attention._id);
    console.log("newNotifications ", newNotifications);
    props.setNewNotifications(newNotifications);
  }

  return (
    <Fragment>
      <List
        {...rest}
        className={clsx(classes.root, className)}
        disablePadding
      >
        {notifications.map((notification, i) => (
          <ListItem
            className={classes.listItem}
            component={RouterLink}
            divider={i < notifications.length - 1}
            key={notification.id}
            to="#"
            onClick={onShowDetailAttention(notification.id)}
          >
            <ListItemAvatar>{avatars[notification.type]}</ListItemAvatar>
            <ListItemText
              primary={notification.title}
              primaryTypographyProps={{ variant: 'body1' }}
              secondary={notification.status}
            />
            <ArrowForwardIcon className={classes.arrowForwardIcon} />
          </ListItem>
        ))}
      </List>
      <Dialog
        fullWidth maxWidth="lg"
        open={openDetailAttention}
        onClose={closeDetailAttention}
        TransitionComponent={Transition}>
        <DialogContent>
          <AttentionDetail attention={attention} fromNotification={true} onRemoveItem={onRemoveItem} onClose={closeDetailAttention} />
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={closeDetailAttention} color="primary">
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default NotificationList;
